import React from 'react'
import { Button, ListGroup, ListGroupItem } from 'reactstrap'
import PerfectScrollbar from 'react-perfect-scrollbar'
import { X, Layers, Star, Bookmark, Check, Trash } from 'react-feather'
import { connect } from 'react-redux'
import { changeFilter } from '../../redux/actions/diary/index'
const DiarySidebar = (props) => {
  return (
    <React.Fragment>
      <span
        className="sidebar-close-icon"
        onClick={() => props.mainSidebar(false)}
      >
        <X size={15} />
      </span>
      <div className="todo-app-menu">
        <div className="add-task">
          <Button.Ripple
            block
            className="btn-block"
            color="warning"
            onClick={() => {
              props.addTask('open')
              props.mainSidebar(false)
            }}
          >
            Add Notes
          </Button.Ripple>
        </div>
        <PerfectScrollbar
          className="sidebar-menu-list"
          options={{
            wheelPropagation: false
          }}
        >
          <ListGroup className="font-medium-1">
            <ListGroupItem
              className="border-0 pt-0"
              action
              onClick={() => {
                props.changeFilter('all')
              }}
              active={
                props.routerProps.location.pathname === '/diary' ? true : false
              }
            >
              <Layers size={22} />
              <span className="align-middle ml-1">All</span>
            </ListGroupItem>
          </ListGroup>
          <hr />
          <h5 className="mt-2 mb-1 pt-25">Filters</h5>
          <ListGroup className="font-medium-1">
            <ListGroupItem
              className="border-0"
              onClick={() => {
                props.changeFilter('important')
              }}
              active={
                props.routerProps.location.pathname === '/diary/important'
                  ? true
                  : false
              }
            >
              <Star size={22} />
              <span className="align-middle ml-1">Important</span>
            </ListGroupItem>
            <ListGroupItem
              className="border-0"
              onClick={() => {
                props.changeFilter('starred')
              }}
              active={
                props.routerProps.location.pathname === '/diary/starred'
                  ? true
                  : false
              }
            >
              <Bookmark size={22} />
              <span className="align-middle ml-1">Bookmark</span>
            </ListGroupItem>
            <ListGroupItem
              className="border-0"
              onClick={() => {
                props.changeFilter('completed')
              }}
              active={
                props.routerProps.location.pathname === '/diary/completed'
                  ? true
                  : false
              }
            >
              <Check size={22} />
              <span className="align-middle ml-1">Completed</span>
            </ListGroupItem>
            <ListGroupItem
              className="border-0"
              onClick={() => {
                props.changeFilter('trashed')
              }}
              active={
                props.routerProps.location.pathname === '/diary/trashed'
                  ? true
                  : false
              }
            >
              <Trash size={22} />
              <span className="align-middle ml-1">Trashed</span>
            </ListGroupItem>
          </ListGroup>
          <hr />
          <h5 className="mt-2 mb-1 pt-25">Labels</h5>
          <ListGroup className="font-medium-1">
            <ListGroupItem
              className="border-0"
              onClick={() => {
                props.changeFilter('personal')
              }}
              active={
                props.routerProps.location.pathname === '/diary/personal'
                  ? true
                  : false
              }
            >
              <span className="bullet bullet-primary align-middle" />
              <span className="align-middle ml-1">Personal</span>
            </ListGroupItem>
            <ListGroupItem
              className="border-0"
              onClick={() => {
                props.changeFilter('professional')
              }}
              active={
                props.routerProps.location.pathname === '/diary/professional'
                  ? true
                  : false
              }
            >
              <span className="bullet bullet-warning align-middle" />
              <span className="align-middle ml-1">Professional</span>
            </ListGroupItem>
            <ListGroupItem
              className="border-0"
              onClick={() => {
                props.changeFilter('others')
              }}
              active={
                props.routerProps.location.pathname === '/diary/others'
                  ? true
                  : false
              }
            >
              <span className="bullet bullet-success align-middle" />
              <span className="align-middle ml-1">Others</span>
            </ListGroupItem>
            <ListGroupItem
              className="border-0"
              onClick={() => {
                props.changeFilter('todo')
              }}
              active={
                props.routerProps.location.pathname === '/diary/todo'
                  ? true
                  : false
              }
            >
              <span className="bullet bullet-danger align-middle" />
              <span className="align-middle ml-1">TO DO List</span>
            </ListGroupItem>
          </ListGroup>
        </PerfectScrollbar>
      </div>
    </React.Fragment>
  )
}

export default connect(null, { changeFilter })(DiarySidebar)
